// ver____Profile.jsx
import React from 'react';
import ProfileBlock from '../components/ProfileBlock';

const blocks = [
  { title: '', content: '  ', direction: 'left' },
  { title: '', content: '', direction: 'right' },
  { title: '', content: '', direction: 'left' },
  { title: '', content: 'Java  JavaScriptPython', direction: 'right' },
];

const Profile = () => (
  <div style={{ padding: '40px 0' }}>
    {blocks.map((block, index) => (
      <ProfileBlock
        key={index}
        title={block.title}
        content={block.content}
        direction={block.direction}
      />
    ))}
  </div>
);

export default Profile;
